.card-stock {
    position: relative;
    margin-top: 30px;
}

.card-stock .card{
    position: absolute;
    top: 0;
    transition: transform .2s ease-in-out, width .3s ease-in-out, left .3s ease-in-out, opacity .3s ease-in-out, background-color .3s ease-in-out;
    opacity: 0;
    z-index: 1;
    -webkit-user-select: none; 
    -moz-user-select: none; 
    -ms-user-select: none; 
    user-select: none; 
    width: 100%;
    height: 300px;
}

.card-stock .card.overlayed:after {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

