}
.card-stock .card.overlayed.no:after {
    /*content: attr(data-left);*/
    /*background: rgba(255,20,20,0.3);*/
}

.card-stock .card:nth-child(4) {
    transform: translateY(10px);
    width: calc(100% - 6px);
    left: 3px;
    z-index: 2;
    opacity: 0;
}

.card-stock .card:nth-child(3) {
    transform: translateY(10px);
    width: calc(100% - 6px);
    left: 3px;
    z-index: 2;
    opacity: 1;
}

.card-stock .card:nth-child(2) {
    transform: translateY(5px);
    width: calc(100% - 4px);
    left: 2px;
    z-index: 3;
    opacity: 1;
}

.card-stock .card:nth-child(1) {
    transform: translateY(0px);
    z-index: 4;
    opacity: 1;
}

.card-stock .btn-wr {
    position: absolute;
    top: 15px;
}
