.fixed-footer-row {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    padding-bottom: 10px;
    left: 0;
    right: 0;
}
    
.buttons-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 480px;
    padding-right: 15px;
    padding-left: 15px;
}

.btn.answer-btn {
    width: calc(50% - 5px);
}